export default {
	props: {
		// 是否显示弹窗
		isShowModal: {
			type: Boolean,
			default: false
		},
		// 是否显示蒙层
		showMasking: {
			type: Boolean,
			default: true
		},
		// 蒙层背景色
		bgColor: {
			type: String,
			default: 'rgba(0,0,0,.6)'
		},
		// 模态框标题
		modalTitle: {
			type: String,
			default: ''
		},
		// 取消按钮文字
		cancelText: {
			type: String,
			default: '取消'
		},
		// 确定按钮文字
		confirmText: {
			type: String,
			default: '确定'
		},
		// 取消按钮文字颜色
		cancelTextColor: {
			type: String,
			default: '#666'
		},
		// 确定按钮文字颜色
		confirmTextColor: {
			type: String,
			default: '#2E3B67'
		}
	},
	data() {
		return {
			// isShowModal: false
		}
	},
	methods: {
		changeMask(){
			this.$emit('onClickChange')
		},
		showModal() {
			this.isShowModal = true
		},
		handleCancel() {
			// this.isShowModal = false
			this.$emit('onClickCancel', false)
		},
		handleConfirm() {
			// this.isShowModal = false
			this.$emit('onClickConfirm', false)
		}
	}
}
